import React from 'react';
import {toggleDropdown} from '../../../services/common-functions';

class HeaderSidebarToggle extends React.Component {
  render(){
    return (
      <li>
        <a data-toggle="control-sidebar" onClick={() => {toggleDropdown(document.getElementsByClassName('control-sidebar')[0], 'control-sidebar-open');}}>
          <i className="fa fa-gears"></i>
        </a>
      </li>
    );
  }
}

export default HeaderSidebarToggle;
